.modal-dialog {
  margin-top: 10vh;
  .modal-content {

    background-color: #31383c;
    .modal-header {
      border-bottom: none;
      .close {
        color: #FFFFFF;
      }
    }
    .modal-body {
      padding: 44px;
      label {
        margin-top: 20px;
        font-family: 'PT Sans';
        font-size: 16px;
        color: #FFFFFF;
      }
      input, textarea {
        background-color: #eeebeb;
        border-radius: 2px;
        &:focus {
          outline: none !important;
          border:1px solid white;
          box-shadow: 0 0 10px #719ECE;
        }
      }
      .send-message-popup {
        float: right;
        display: block;
        width: 240px;
        margin-top: 30px;
        padding: 5px;
        font-size: 24px;
        font-family: 'RobotoCondensed';
        background: transparent;
        border: 2px solid white;
        border-radius: 5px;
        color: white;
        transition: background-color 0.3s ease-in-out;
        &:hover {  background: darken(#95a3aa, 10%);  }
        &:active {  background: darken(#95a3aa, 20%);  }
      }
    }
  }
}



